/* General table styling */
.container {
  width: 100%;
  height: 100dvh;
  background-image: url("./img/aza.png");
  background-size: cover;
  background-repeat: no-repeat;
  /* background-position: center; */
  background-attachment: fixed;
  position: relative;
}

.navbar {
  height: 50px;
  text-align: center;
  padding: 10px;
}
.navbar_brand {
  text-decoration: none;
  color: green;
  font-size: 2rem;
  font-weight: 600;
}
.section {
  width: 100%;
}

.sec_flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}
.user_data {
  font-size: 1.5rem;
}
.user_entry a {
  text-decoration: none;
}

.btn_entry {
  font-size: 1.5rem;
  background-color: #2c3e50;
}
.table_container {
  padding: 0 10px;
  max-height: calc(100dvh - 120px);
  overflow-y: auto;
  overflow-x: hidden;
  scroll-behavior: smooth;
}
.table {
  width: 100%;
  border-collapse: collapse;
  text-align: left;
}
.table #myhead {
  background-color: cadetblue;
  position: sticky;
  top: 0;
  z-index: 2;
}
.table .t_head,
.table .t_data {
  padding: 10px 15px;
}
.table .t_data {
  border: 1px solid lightgray;
  color: #333;
}
.table .t_data a {
  text-decoration: none;
}

.table #mydata tr:nth-child(odd) {
  transition: background-color 0.3s;
}
.table #mydata tr:hover {
  background-color: #b9e792;
}
.btn_danger {
  background-color: #dc3545;
}
.btn_success {
  background-color: #28a745;
}
.btn_view {
  background-color: #17a2b8;
}

/* Responsive styles for smaller screens */
@media (max-width: 768px) {
  .table .t_head,
  .table .t_data {
    padding: 8px;
    font-size: 0.8rem;
  }
  
}

@media (max-width: 576px) {
  .table .t_data {
    font-size: 0.6rem;
    padding: 6px;
  }

 
}

@media (max-width: 450px) {
  .container {
    width: fit-content;
    background-size: cover;
    background-position: center center;
  }
  .table .t_data {
    font-size: 0.6rem;
    padding: 4px;
  }

  
}
