.DUV_btn {
  font-size: 1rem;
  padding: 5px 10px;
  color: white;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  margin: 5px;
  transition: transform 0.2s;
}

.DUV_btn:hover {
  transform: scale(1.1);
}

@media (max-width: 768px) {
  .DUV_btn {
    padding: 4px 8px;
    font-size: 0.8rem;
  }
}
@media (max-width: 576px) {
  .DUV_btn {
    display: block; /* Stack buttons vertically */
    margin-bottom: 5px;
    padding: 4px 10px;
    text-align: center;
  }
  .DUV_btn:last-child {
    margin-bottom: 0;
  }
}
