.container-cuv {
  height: 100dvh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
}

.con-page {
  width: 500px;
  height: 400px;
  background: linear-gradient(to bottom, #ff8722, #e8e8be, #56c956);
  padding: 15px;
  /* margin: auto;
  border: 1px solid #e8e8be;
  border-radius: 5px; */

  margin: auto;
  border: 1px solid #707052;
  border-radius: 5px;
  box-shadow: 1px 1px 147px 20px #00000094;

}

.con-page form {
  display: flex;
  flex-direction: column;
  padding: 10px 8px;
  row-gap: 20px;
}
.form-inp .form-control {
  display: block;
  padding: 0.5rem 0.2rem;
  margin: 0.3rem 0;
  font-size: 1rem;
  border: none;
  border-radius: 5px;
  width: 100%;
  color: #333;
}
.form-inp .form-text {
  color: gray;
}
.btn-cuv {
  display: flex;
  justify-content: space-between;
}
.btn-cuv .btn-primary {
  font-size: 1.1rem;
  padding: 0.4rem 0.65rem;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.btn-ph1 {
  background-color: #007bff;
  color: white;
}
.btn-ph1:hover {
  transition: 0.3s;
  background-color: #2464a9;
}
.btn-ph2 {
  background-color: green;
  color: white;
}
.btn-ph2:hover {
  transition: 0.3s;
  background-color: #255200;
}

/* general css appling on view section */
.card-header {
  border-bottom: 1px solid gray;
  padding: 0.5rem 0;
  color: #990000;
  text-align: center;
}

.view-table {
  padding: 30px;
  font-size: 1rem;
  line-height: 2;
  color: #333;
}
.view-table strong {
  width: 30%;
  text-align: left;
  display: inline-block;
}
.view-table span {
  width: 70%;
  text-align: left;
  display: inline-block;
}
